.App {
    text-align: center;
  }
  
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 10vmin;
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 10vh;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

  .Login{
    flex-direction: row;
    width: 8vw;
  }

  .Body {
    flex-direction: row;
    background-size: 100vmax;
    background-position: center;
    background-repeat: no-repeat;
    color: #ff6000;
    font-size: calc(10px + 1vmin);
  }

  .SideBar{
    flex-direction: column;
    height: 100vh;
    background-color: gray
  }

  .MainContent {
    flex-direction: column;
    height: 100vh;
    background-color: #ffffff;
  }

  .Code {
    background-color: #999999;
  }

  .LoginButton {
    margin: 1vmin;
    width: 10vw;
    min-width: 100px;
  }

  .Button {
    margin-top: 1vmin;
    width: 7vw;
  }

  .Rules {
    color: #000000
  }

  .ListView {
    overflow: 'auto';
    min-height: 200px; 
    width: 40vw;
    margin-top: 6vh;
    margin-bottom: 6vh;
    margin-left: 4vw;
    margin-right: 4vw;
  }

  .ListTile {
    text-align: left;
    background-color: #666666;
    min-height: 90px;
    height: 10vh;
    font-size: 10 + 2vmin
  }